
#vueapp {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
}
.demo-card-wide.mdl-card {
  width: 512px;
}

.demo-card-wide>.mdl-card__title {
  color: #fff;
  height: 176px;
  background: url('https://i.gifer.com/Xbs3.gif') center / cover;
}

.demo-card-wide>.mdl-card__menu {
  color: #fff;
}

/* Overriding default sidebar spacing */

main {
  margin-left: 0px !important;
}

header {
  margin-left: 0px !important;
  width: 100% !important;
}

#logo {
  font-size: 60px;
  margin: 40px auto 20px;
}

#mainSpinner {
  position: fixed;
  top: 0px;
  bottom: 0px;
  left: calc(50% - 14px);
  margin: auto;
  z-index: 10;
}

/*
@media only screen and (min-width: 1025px) {
#mainSpinner {
    left: calc(50% + 106px);
}
}*/

.handle-card {
  padding: 20px 50px;
  margin: auto;
  max-height: 250px;
  clear: both;
  text-align: center;
}

.card,
.demo-card-wide {
  margin: 10px auto;
  overflow-y: hidden;
  overflow-x: auto;
  background: #fff;
}

.card,
.handle-card,
.share-div,
.fb-share-button {
  border-radius: 5px;
}

td {
  color: rgb(117, 117, 117);
  font-weight: 500;
}

table {
  width: 100%;
  border-radius: 5px;
}

a {
  text-decoration: none;
}

/* for footer */

.sharethis {
  text-transform: uppercase;
  font-weight: 600;
  color: white;
  padding: 6px 18px;
  border-radius: 5px;
  background-color: #4267b2;
}

.sharethis:hover {
  background-color: #365899;
}

.fb_iframe_widget_fluid {
  display: inline-block !important;
}

.fb-recommend {
  padding: 10px;
}

.sharethis,
.fb-save,
.fb-recommend {
  /*box-shadow: 1px 1px 2px black;*/
  border-radius: 5px;
}

.share-div {
  text-align: center;
  /*background-color: #37474f;*/
  min-height: 200px;
  margin-top: 20px;
  /*padding: 30px 0px;*/
}

.vertical-space {
  width: 100%;
  height: 20px;
}

/*only for the about page*/

.fb-comments {
  width: 100%;
}

.comments-con {
  padding: 30px;
  background-color: white;
  border-radius: 5px;
  margin: auto;
}

.about {
  text-align: center;
  margin: auto;
  border-radius: 50%;
  background-color: white;
  padding: 40px;
  margin-bottom: 20px;
}

/* only for index */

#unsolvedCon {
  padding: 30px 20px 50px 50px;
  background-color: white;
}

#unsolvedTitle,
#commonContestTitle {
  color: #393939;
  font-weight: 500;
  font-size: 20px;
  margin: -20px;
}

#heatmapCon {
  padding: 30px;
}

#heatmapTitle {
  font-size: 20px;
  color: #393939;
}

#heatmapMaxValue {
  text-align: center;
}

.heatmap-text {
  font-size: 12px;
  text-align: center;
  width: 155px;
}

.lnk {
  margin: 2px 5px;
  float: left;
}

/* only for compare */

#submitButton {
  margin: auto;
}

.handle1Color {
  color: #009688;
}

.handle2Color {
  color: #3F51B5;
}

#commonSolvedTable {
  width: 250px;
}

/* only for virtual */

#info {
  padding: 40px;
}

.input-card {
  padding: 20px 50px;
  margin: auto;
  clear: both;
  text-align: center;
}

/* scrollbar (only for chrome and safari) */

::-webkit-scrollbar {
  height: 5px;
  width: 5px;
}

::-webkit-scrollbar-track {
  display: none;
}

::-webkit-scrollbar-thumb {
  border-radius: 5px;
  background-color: #607D8B;
}

/*things I don't know much about*/

html,
body {
  font-family: 'Roboto', 'Helvetica', sans-serif;
}

.layout .mdl-layout__header .mdl-layout__drawer-button {
  color: rgb(0, 0, 0);
}

.layout .mdl-layout__header .mdl-layout__drawer-button .material-icons {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  padding: 12px;
  height: 170px;
}

.mdl-navigation {
  height: 50px;
}

.mdl-layout__drawer .avatar {
  margin-bottom: 16px;
}

.drawer {
  border: none;
}

/* iOS Safari specific workaround */

.drawer .mdl-menu__container {
  z-index: -1;
}

.drawer .navigation {
  z-index: -2;
}

/* END iOS Safari specific workaround */

.drawer .mdl-menu .mdl-menu__item {
  display: -ms-flexbox;
  display: -webkit-box;
  display: flex;
  -ms-flex-align: center;
  -webkit-box-align: center;
          align-items: center;
}

.drawer-header {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  display: -ms-flexbox;
  display: -webkit-box;
  display: flex;
  -ms-flex-direction: column;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
          flex-direction: column;
  -ms-flex-pack: end;
  -webkit-box-pack: end;
          justify-content: flex-end;
  padding: 16px;
  height: 151px;
}

.navigation {
  -ms-flex-positive: 1;
  -webkit-box-flex: 1;
          flex-grow: 1;
}

.mdl-layout__title {
  color: black;
  font: bold;
  font-size: 25px;
}

.layout .navigation .mdl-navigation__link {
  display: -ms-flexbox !important;
  display: -webkit-box !important;
  display: flex !important;
  -ms-flex-direction: row;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
          flex-direction: row;
  -ms-flex-align: center;
  -webkit-box-align: center;
          align-items: center;
  color: #f7a4a4;
  font-weight: 500;
}

.layout .navigation .mdl-navigation__link:hover {
  background-color: #00BCD4;
  color: #00BCD4;
}

.navigation .mdl-navigation__link .material-icons {
  font-size: 24px;
  color: rgba(255, 255, 255, 0.56);
  margin-right: 32px;
}

.content {
  max-width: 1080px;
}

.mdl-layout__drawer {
  background: rgba(0, 0, 0, 0.795);
  color: rgb(255, 255, 255);
}

.mdl-layout__drawer .mdl-navigation .mdl-navigation__link:hover {
  color: rgb(255, 255, 255);
}















































































































































body {
}
.picker-div {
  
  width: auto;
  padding-top: 5em;
  padding-left: 1em;
  padding-right: 1em;
}
.removable-handle {
  float: right;
  cursor: pointer;
}
.column1,
.column2 {
  /* width: auto; */
  min-width: calc(100% / 2 - 20px);
  max-width: calc(100% / 2 - 20px);
  /* padding: 10px; */
  overflow: auto;
  display: inline-block;
}
.column1 {
  float: left;
}
.column2 {
  float: right;
}
@media only screen and (max-width: 720px) {
.column1,
  .column2 {
    width: auto;
    max-width: inherit;
    margin: 0 auto;
    float: none;
    display: block;
}
}

